.social {
  display: flex;
  flex-wrap: wrap;

  padding: 0;
  margin: 0;
  list-style: none;

  &--vert {
    & .social__item {
      margin-bottom: 11px;
      margin-right: 0;
    }
  }

  &__item {
    margin-right: 28px;

    &:last-child {
      margin-right: 0;
    }

    @media (max-width: $table) {
      margin-right: 31px;
    }
  }

  &__link {
    display: block;

    &:focus,
    &:hover {
      opacity: 0.5;
    }
  }

  & svg {
    height: 18px;

    @media (max-width: $table) {
      width: 31px;
      height: 31px;
    }
  }
}
