.main-promo {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;

  margin-top: -7px;

  width: 100%;
  color: $default-color;
  text-align: right;
  box-sizing: border-box;

  @media (max-width: $desktop) {
    margin-top: 0;
  }

  &__info {
    display: flex;
    flex-wrap: wrap;

    align-items: center;

    width: 83%;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      justify-content: space-between;
      padding-top: 16px;
      width: 100%;
    }
  }

  &__social {
    width: 12px;
    margin-bottom: 30px;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      display: none;
    }
  }

  &__content {
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 48%;
    padding-top: 120px;
    padding-right: 67px;
    box-sizing: border-box;

    @media (max-width: 1300px) {
      padding-right: 30px;
    }

    @media (max-width: $desktop) {
      width: 50%;
      padding-top: 54px;
      padding-right: 15px;
    }

    @media (max-width: $table) {
      align-items: center;
      width: 100%;
      padding-right: 0;
    }
  }

  &__header {
    position: relative;
    margin-bottom: 58px;

    max-width: 385px;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      margin-bottom: 43px;
      max-width: 325px;
    }

    @media (max-width: $table) {
      margin-bottom: 48px;
      max-width: 320px;
    }
  }

  &__pic {
    width: 52%;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      width: 48%;
      min-height: 391px;
    }

    @media (max-width: $table) {
      display: none;
    }

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__city {
    margin-bottom: 7px;
    font-family: $mainFontTitle;
    font-style: normal;
    font-weight: normal;
    font-size: 49px;
    line-height: 49px;
    letter-spacing: 0.03em;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      margin-bottom: 3px;
      font-size: 34px;
      line-height: 34px;
    }

    @media (max-width: $table) {
      margin-bottom: 6px;
      font-size: 28px;
      line-height: 28px;
    }
  }

  &__desc {
    position: absolute;
    left: 0;
    bottom: 10%;
    display: block;
    max-width: 125px;
    font-size: 14px;
    line-height: 150%;
    text-align: right;
    box-sizing: border-box;

    @media (max-width: $desktop) {
      top: 64%;
      left: -29px;
    }

    @media (max-width: $table) {
      top: 64%;
      left: -10px;
    }
  }

  & h1 {
    margin: 0;
    font-family: $mainFontTitle;
    font-weight: bold;
    font-size: 109px;
    line-height: 109px;
    text-align: right;
    letter-spacing: 0.03em;

    color: $default-color;
    box-sizing: border-box;

    text-transform: uppercase;

    @media (max-width: $desktop) {
      max-width: 309px;
      font-size: 86px;
      line-height: 86px;
    }

    @media (max-width: $table) {
      font-size: 78px;
      line-height: 78px;
    }
  }

  &__link {
    display: inline-block;
    vertical-align: middle;
    padding: 20px 55px;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;

    letter-spacing: 0.05em;
    text-transform: uppercase;
    background-color: transparent;
    text-decoration: none;

    color: #ffffff;
    border: 4px solid #ffffff;

    box-sizing: border-box;
    text-align: center;

    &:focus,
    &:hover {
      opacity: 0.5;
    }

    @media (max-width: $desktop) {
      padding: 20px 32px;
      max-width: 300px;
    }

    @media (max-width: $table) {
      width: 100%;
    }
  }
}
